import React from 'react';
import { Link } from 'react-router-dom';
import classes from './Header.module.css';

const Header = ({ theme, isLogin, onLogout }) => {
  return (
    <header
      className={`${classes.header} ${
        theme === 'light' ? `${classes.light}` : ''
      }`}
    >
      <div className="center">
        <div className={classes.upper}>
          <h1 className={classes.logo}>
            <Link to="/">chova cinema</Link>
          </h1>
          <ul className={`${classes.menu} ${classes.menu1}`}>
            <li>
              <a href="##">
                <span>
                  <i className="fab fa-facebook"></i>
                </span>{' '}
                
              </a>
            </li>
            <li>
              <a href="##">
                <span>
                  <i className="fab fa-youtube"></i>
                </span>{' '}
                
              </a>
            </li>
          </ul>
          <ul className={`${classes.menu} ${classes.menu2}`}>
            <li>
              <a href="##"></a>
            </li>
            <li>
              <a href="##"></a>
            </li>
            <li>
              {!isLogin ? (
                <Link to="/login"></Link>
              ) : (
                <Link to="/" onClick={onLogout}>
                  
                </Link>
              )}
            </li>
          </ul>
        </div>
        <nav className={classes.navigation}>
          <ul className={classes.links}>
            <li>
              <Link to="/ticketing"></Link>
            </li>
            <li>
              <Link to="/movies"></Link>
            </li>
            <li>
              <a href="##"></a>
            </li>
            <li>
              <a href="##"></a>
            </li>
            <li>
              <a href="##"></a>
            </li>
            <li>
              <a href="##">VOD</a>
            </li>
          </ul>
          <ul className={`${classes.menu} ${classes.menu3}`}>
            <li>
              {!isLogin ? (
                <Link to="/signin">
                  <span>
                    <i className="fas fa-user"></i>
                  </span>{' '}
                  
                </Link>
              ) : (
                <Link to="/MyPage">
                  <span>
                    <i className="fas fa-user"></i>
                  </span>{' '}
                  
                </Link>
              )}
            </li>
            <li>
              <Link to="/ticketing">
                <span>
                  <i className="fas fa-bookmark"></i>
                </span>{' '}
                
              </Link>
            </li>
            <li>
              <button className={classes['btn-menu']}>
                <span className={classes.icon}>
                  <i className="fas fa-bars"></i>
                </span>
              </button>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
};

export default Header;
